<template>
    <div>
      <h1>create vue _ list</h1>
      <ul class="list-group list-group-flush container-lg zero-padding border border-secondary rounded">
      <li
        v-for="(item, index) in listdata"
        :key="index"
        class="list-group-item list-group-item-action row justify-content-btween border border-0 zero-margin"
      >
        <label class="col-10">
          id : {{ index }}, 제목 : {{ item.title }}, 내용 : {{ item.content }}
        </label>
        <button @click="editData(index)" class="btn btn-warning btn-sm col-1">
          수정
        </button>
        <button @click="del_data(index)" class="btn btn-danger btn-sm col-1">
          삭제
        </button>
      </li>
    </ul>
    </div>
  </template>
  
  <script>
  export default {
    props : {
        listdata : Array
    },
    data (){
        return {

        }
    },
    mounted () {
        console.log(this.listdata);
    },
    methods : {
        editData (index){
            let data = {
                index : index,
                title : this.listdata[index].title,
                content : this.listdata[index].content
            }
            this.$emit("editData",data);
        },
        del_data (){

        }
    }
  }
  </script>
  
