    </header>
  </div>
  <main>
    <!-- add data :  -->
    <addDataView @addDatas="addDatas($event)"></addDataView>
    <!-- emit
      @emit 자식 => 부모
      자식 : this.$emit("이름", 값);
      부모 : 해당 자식 컨포넨츠에서 @이름 = "함수명($event)" => methods에서 함수명 (데이터) {}
    -->
    <!-- list of data : appear when have more than 1 data -->
    <listDataView :listdata="data_arr" @editData="editData($event)"></listDataView>
    <!-- add에서 추가 된 값을 배열로 listdataview로 넘김 (모->자)-->
    <!-- list에서 수정하기로 선택 된 배열을 editdataview을 받음 (자->모) -->
    <!--  props
      <컴포넌츠 :이름="뷰의 데이터값"></컴포넨츠> -->
    <!-- <컴포넌츠 이름="값"></컴포넨츠> -->
    <!-- editing box : appear when click the edit button -->
    <editDataView :editData="editing_data" @e_data="e_data($event)"></editDataView>
    <!-- list에서 수정하기로 선택 된 배열을 editdataview로 넘김 (모->자)-->
  </main>
</template>

<script>
import addDataView from './views/addDataView.vue';
import listDataView from './views/listDataView.vue';
import editDataView from './views/editDataView.vue';
export default {
  name: "App",
  components: {
    addDataView,
    listDataView,
    editDataView
  },
  data() {
    return {
      title: "",
      content: "",
      editing: false,
      data_arr: [],
      editing_data: {},
    };
  },
  methods: {
    addDatas (data){ //addDataView에서 가져 온 title/content의 값
      this.data_arr.push(data);
    },
    editData (data){//listData에서 가져 온 선택 된 index/title/content의 값
      console.log("edit 앱뷰",data)
      // this.editing_data.push(data);
      this.editing_data = data;
      console.log("editing_data appvue 여기",this.editing_data)
    },
    e_data (e_data) {//EditData에서 가져 온 수정 된 index/title/content의 값
      this.data_arr[e_data.index].title = e_data.title;
      this.data_arr[e_data.index].content = e_data.content;
      this.editing_data = {};
    },
    add_data() {
      if (!this.title) {
        alert("제목을 입력하세요.");
        document.getElementById("title").focus();
        return;
      }
      if (!this.content) {
        alert("내용을 입력하세요.");
        document.getElementById("content").focus();
        return;
      }
      let data = {
        title: this.title,
        content: this.content,
      };
      this.data_arr.push(data);
      this.title = "";
      this.content = "";
    },
    edit_btn(index) {
      console.log(index);
      this.editing = true;
      this.editing_data = {
        title: this.data_arr[index].title,
        content: this.data_arr[index].content,
        index: index,
      };
    },
    edit_save(index) {
      this.data_arr[index].title = this.editing_data.title;
      this.data_arr[index].content = this.editing_data.content;
      this.editing_data = {};
      this.editing = false;
    },
    close_edit() {
      this.editing_data = {};
      this.editing = false;
    },
    del_data(index) {
      if (confirm("삭제할까요?")) {
        this.data_arr.splice(index, 1);
        console.log(this.data_arr);
        console.log(index);
      }
    },
  },
};
</script>
