<template>
    <div>
      <h1>create vue _ edit</h1>
      <!-- 
        컴포넌츠에서 props로 받은 값은 v-model로 사용이 불가능~
        구조분해할당..등으로 별도의 데이터로 받는 과정이 필요..
        예) computed에서 임의의 함수에서 return할 수 있게 하기.
        예2) 자식창이 열릴 때 값을 따로.. 줄 수 있도록..
       -->
      <div class="mb-3 container-lg bg-secondary bg-opacity-50 rounded">
      <div class="row">
        <label class="col-10">게시글 수정</label>
        <button
          @click="edit_save(e_datas.index)"
          class="btn btn-warning btn-sm col-1"
        >
          수정
        </button>
        <button @click="close_edit()" class="btn btn-danger btn-sm col-1">
          닫기
        </button>
      </div>
      <div class="row">
        <label class="form-label col">제목</label>
        <input v-model="e_datas.title" class="form-control col-6" />
      </div>
      <div class="row">
        <label class="form-label col">내용</label>
        <textarea
          v-model="e_datas.content"
          class="form-control col-6 textarea-size"
        ></textarea>
      </div>
    </div>
    </div>
  </template>
  
  <script>
  export default {
    props : {
        editData : Object
    },
    data () {
        return {
            e_datas : {}
        }
    },
    computed : {
        // getdata (){
        //     let data = {...this.editData};
        //     return data
        // }
    },
    updated (){
        console.log("updated")
        this.e_datas = this.editData;
    },
    mounted () {
        // console.log("editpage 수정페이지", this.editData);
    },
    methods : {
        edit_save (index){
            // console.log(index, this.getdata)
            let e_data = {
                index : index,
                title : this.e_datas.title,
                content : this.e_datas.content
            }
            // this.e_datas = {}
            this.$emit("e_data",e_data);
        },
        close_edit (){

        }
    }
  }
  </script>
  
