<template>
  <div>
    <h1>create vue _ add</h1>
    <!-- content part -->
    <div class="container-lg bg-light rounded">
      <div class="mb-3 row">
        <label class="col-11">게시글 등록</label>
        <button @click="emitdata"  class="btn btn-success btn-sm col-1">
          저장
        </button>
      </div>
      <div class="row">
        <label class="form-label">제목</label>
        <input v-model="title" id="title" class="form-control" />
      </div>
      <div class="row">
        <label class="form-label">내용</label>
        <textarea
          v-model="content"
          id="content"
          class="form-control textarea-size"
        ></textarea>
      </div>
    </div>
  </div>
</template>

<script>
export default {
 data () {
    return {
        title : "",
        content : ""
    }
 },
 methods : {
    emitdata (){
        let data = {
            title : this.title,
            content : this.content
        }
        this.$emit("addDatas",data);
        this.title = "";
        this.content = "";
    }
 }
}
</script>

